@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
html *{font-family:'Cutive Mono', monospace}

.App{width:100%;height:auto;min-height:100vh;padding:1.5rem;background-color:rgba(224,224,224,0.541176)}.App h1{text-align:center;font-size:45px;margin-bottom:.5rem}.App small{display:block;text-align:center;margin-bottom:4rem}.App__content{margin-top:3rem}.App__content .results{display:grid;grid-template-columns:repeat(auto, 1fr);grid-template-rows:auto;grid-gap:2rem}@media (min-width: 768px){.App__content .results{grid-template-columns:repeat(2, 1fr);margin:2rem}}@media (min-width: 1024px){.App__content .results{grid-template-columns:repeat(3, 1fr);margin:5rem 0}}@media (min-width: 1400px){.App__content .results{grid-template-columns:repeat(5, 1fr)}}@media (min-width: 1024px){.App__content{margin:3rem 5rem}}@media (min-width: 768px){.App{padding:3rem}}

.Input{width:100%;margin:2rem auto;box-shadow:0px 1px 10px 0px rgba(146,146,146,0.7)}.Input .form-control:focus{box-shadow:none}@media (min-width: 768px){.Input{width:60%}}

.Movie{background-color:white;border:1px solid #bbb;display:grid}.Movie img{display:block;width:100%;height:20rem;object-fit:cover}.Movie p{font-size:1rem;margin-bottom:10px;line-height:1.1em}

